<template>
  <div
    v-for="(question, key) in questions"
    :key="key"
    style="margin-bottom: 20px; clear: both"
  >
    <div class="dendam dt">Câu số {{ question.STT }} :</div>
    <div v-if="question.QuestionType === 1">
      {{ question.NameQues }}
    </div>
    <div v-else><img :src="'https://testiq.vn/' + question.NameQues" /></div>

    <div class="ans">
      <template v-for="answer in question.Answers" :key="answer.IDAns">
        <template v-if="answer.AnswerType == 1">
          <input
            type="radio"
            :checked="answer.IDAns === question.CorrectAnswer"
            disabled
            style="color: red"
          />
          <span v-if="answer.AnswerType == 1" style="padding-right: 20px">{{ answer.NameAns }}</span>
        </template>
        <template v-else>
          <div
            class="ans1 dapan62"
            :style="{
              backgroundImage: `url(${'https://testiq.vn/' + answer.NameAns})`,
              paddingBottom: 0,
              marginBottom: 0,
              height: '180px',
              borderColor: answer.IDAns === question.CorrectAnswer ? 'red' : 'black'
            }"
            onclick="luudapan(62,246,this,5)"
          ></div>
        </template>
      </template>
    </div>
  </div>
</template>

<script setup>
import { onMounted, ref } from "vue";
import ques from "./assets/4.json";
import answer from "./assets/4-answer.json";

const questions = ref(null);

var groupBy = function (xs, key) {
  return xs.reduce(function (rv, x) {
    (rv[x[key]] = rv[x[key]] || []).push(x);
    return rv;
  }, {});
};

onMounted(() => {
  let a = groupBy(ques, "STT");
  Object.keys(a).forEach((key) => {
    a[key] = {
      STT: a[key][0].STT,
      IDQues: a[key][0].IDQues,
      NameQues: a[key][0].NameQues,
      QuestionType: a[key][0].QuestionType,
      CorrectAnswer: answer.find((e) => e.IDQues === a[key][0].IDQues).IDAns,
      Answers: a[key].map((ques) => ({
        IDAns: ques.IDAns,
        NameAns: ques.NameAns,
        AnswerType: ques.AnswerType,
      })),
    };
  });

  console.log(a);

  questions.value = a;
});
</script>

